      <van-icon name="arrow-left" @click="back()"></van-icon>
      <div>自动检测语言</div>

    </div>

    <div class="inp">
      <el-button type="primary">你好</el-button>
      <input type="text" id="showresult" class="showresult">
      <input type="button" value="录音" @click="voiceBegin()">
      <input type="button" value="结束录音" @click="voiceEnd()">
      <van-field v-model="value" placeholder="在此输入要翻译的文本" @input="getTranslate()" @clear="this.result='' " />
    </div>
    <div class="res">
